import React, { Component } from 'react'
import { Flex, WingBlank, WhiteSpace, NavBar, Toast } from 'antd-mobile'

import { Link } from 'react-router-dom'

import styles from './index.module.css'
import { login } from '../../utils/api/user'
import { setToken } from '../../utils'

import { withFormik } from 'formik'
import * as yup from 'yup'

// 
const REG_UNAME = /^[a-zA-Z_\d]{5,8}$/
const REG_PWD = /^[a-zA-Z_\d]{5,12}$/

/**
 * 
 * = =
 * =1. value 2. onChange
 */

class Login extends Component {
  // 
  // state = {
  //   // 
  //   username: '',
  //   // 
  //   password: '',
  // }

  // 
  // handlerInput = (e) => {
  //   // console.log(e.target)
  //   this.setState({
  //     // key:val
  //     // []:val
  //     [e.target.name]: e.target.value,
  //   })
  // }

  // 
  // login = async (e) => {
  //   // 
  //   e.preventDefault()
  //   // 
  //   const { username, password } = this.state
  //   // console.log(e, username, password)
  //   const { status, data, description } = await login({ username, password })
  //   // console.log(status, data)
  //   if (status === 200) {
  //     Toast.success(description, 1, () => {
  //       // =
  //       this.props.history.push('/home/profile')
  //     })
  //     // token
  //     setLocalData(HZW_TOKEN, data.token)
  //   } else {
  //     Toast.fail(description)
  //   }
  // }

  render() {
    // 
    const {
      values, // 
      touched,
      errors,
      handleChange, // 
      handleBlur,
      // 
      handleSubmit,
    } = this.props
    // console.log(this.props)
    // console.log(errors)
    return (
      <div className={styles.root}>
        {/* <h1>{errors.username}</h1> */}
        {/*  */}
        <NavBar mode="light"></NavBar>
        <WhiteSpace size="xl" />

        {/*  */}
        <WingBlank>
          <form onSubmit={handleSubmit}>
            <div className={styles.formItem}>
              <input
                value={values.username}
                name="username"
                onChange={handleChange}
                className={styles.input}
                placeholder=""
              />
            </div>
            {/* 58 */}
            {errors.username && (
              <div className={styles.error}>{errors.username}</div>
            )}
            <div className={styles.formItem}>
              <input
                value={values.password}
                name="password"
                onChange={handleChange}
                className={styles.input}
                type="password"
                placeholder=""
              />
            </div>
            {/* 512 */}
            {errors.password && (
              <div className={styles.error}>{errors.password}</div>
            )}
            <div className={styles.formSubmit}>
              <button className={styles.submit} type="submit">
                 
              </button>
            </div>
          </form>
          <Flex className={styles.backHome}>
            <Flex.Item>
              <Link to="/registe">~</Link>
            </Flex.Item>
          </Flex>
        </WingBlank>
      </div>
    )
  }
}

const NewLogin = withFormik({
  //  => =state
  // namevalues ==== username: 'test2', password: 'test2'
  mapPropsToValues: () => ({ username: 'test2', password: 'test2' }),

  // Custom sync validation
  // 
  // 1. 
  // validate: (values) => {
  //   console.log(values)
  //   const errors = {}

  //   if (!values.username) {
  //     errors.username = ''
  //   }

  //   return errors
  // },
  // 2.  => yupschema
  validationSchema: yup.object().shape({
    username: yup
      .string()
      .required('')
      .matches(REG_UNAME, '58'),
    password: yup
      .string()
      .required('')
      .matches(REG_PWD, '512'),
  }),
  // 
  handleSubmit: async (values, { props }) => {
    // console.log(formikBag)
    // 
    // console.log(values)
    // // 
    // e.preventDefault()
    // 
    const { username, password } = values
    // console.log(e, username, password)
    const { status, data, description } = await login({ username, password })
    // console.log(status, data)
    if (status === 200) {
      console.log(this)
      Toast.success(description, 1, () => {
        // = =>Loginthis
        // backUrl=backUrl
        if (props.location.backUrl) {
          props.history.replace(props.location.backUrl)
        } else {
          // 
          props.history.replace('/home/profile')
        }
      })
      // token
      setToken(data.token)
    } else {
      Toast.fail(description)
    }
  },
})(Login)
// withFormik=
export default NewLogin
