import React, { useState, useEffect, useContext, useRef } from 'react'
// hook
import { useDrop } from 'react-use'
import useSpring from 'react-use/lib/useSpring'

import './index.scss'

/**
 * 
 * Context
 * 1. ContextReact.createContext()
 * 2. ContextProvider=>value
 * 3. ContextConsumer
 */
const MyContext = React.createContext()

/**
 * react-hookclass=state()
 * hook:==useXXX()
 * 
 * 1. 
 * 2. hook
 */

//  use  Hook Hook
// ==>render-propshocUI
const useCounter = () => {
  const [count, setCount] = useState(0)
  // componetDidMount
  useEffect(() => {
    let id = setInterval(() => {
      setCount((c) => c + 1)
    }, 1000)
    // 
    return () => clearInterval(id)
  }, [])
  return [count, setCount]
}

const Child = () => {
  // hookcontext
  const shareCount = useContext(MyContext)
  // console.log(shareCount)

  // hook
  const [count] = useCounter()
  return (
    <div>
      <h3></h3>
      <p style={{ color: 'red' }}>{count}</p>
      <MyContext.Consumer>{(val) => <p>{val}</p>}</MyContext.Consumer>
      <h4>{shareCount}</h4>
    </div>
  )
}

const List = () => {
  // 
  const [list, setList] = useState([{ id: 0, name: '' }])

  // useRef
  let ulRef = useRef()
  console.log(ulRef)

  // hook
  const [count, setCount] = useCounter()

  return (
    <div>
      <h1 style={{ color: 'blue' }}>{count}</h1>
      <ul ref={ulRef}>
        {list.map((item) => (
          <li key={item.id}>{item.name}</li>
        ))}
      </ul>
      <button
        onClick={() => {
          setList((list) => {
            let _list = [
              { id: list.length + 1, name: (Math.random() * 10).toString(16) },
              ...list,
            ]
            return _list
          })
        }}>
        add
      </button>
    </div>
  )
}

