/**
 * =
 */
import React from 'react'

import { Toast } from 'antd-mobile'

import Filter from './components/Filter'
// 
import styles from './index.module.css'
import { getCurrCity } from '../../utils'
import { getListByFilter } from '../../utils/api/house'
import { List, AutoSizer, InfiniteLoader } from 'react-virtualized'
import HouseItem from '../../components/HouseItem'
import { BASE_URL } from '../../utils/request'
import NoHouse from '../../components/NoHouse'


export default class HouseList extends React.Component {

  state = {
    // 
    list: [],
    // 
    count: 0
  }


  // 
  onFilterSel = (filters) => {
    console.log('', filters)
    // 
    this.filters = filters
    // =
    this.getHouseList()
  }

  async componentDidMount () {
    //  ID
    let { value } = await getCurrCity()
    this.cityId = value
    // 
    this.getHouseList()
  }

  // 
  async getHouseList () {
    const { status, data: { list, count } } = await getListByFilter(this.cityId, this.filters)
    // console.log(status, data)
    if (status === 200) {
      // 
      count > 0 && Toast.success(`${count}`)

      this.setState({
        list,
        count
      })
    }
  }

  // item
  rowRenderer = ({
    key, // Unique key within array of rows
    index, // Index of row within collection
    isScrolling, // The List is currently being scrolled
    isVisible, // This row is visible within the List (eg it is not an overscanned row)
    style, // Style object to be applied to row (to position it)
  }) => {
    // 
    const { list } = this.state
    // 
    let item = list[index]
    // =
    if (!!item) {
      // 
      item.src = `${BASE_URL}${item.houseImg}`
      return (
        <HouseItem onClick={() => {
          this.props.history.push({ pathname: '/detail/' + item.houseCode, data: { a: 123 }, abc: 100 })
        }} {...item} key={key} style={style} />

      )
    } else {
      // =
      return (
        <div style={style} key={key}>
          <p className={styles.loading}></p>
        </div>
      )
    }

  }

  // 
  isRowLoaded = ({ index }) => {
    return !!this.state.list[index]
  }

  // =>
  /**
   * 
   * @param {*} startIndex-stopIndex  
   */
  loadMoreRows = ({ startIndex, stopIndex }) => {
    // console.log(startIndex, stopIndex)
    return getListByFilter(this.cityId, this.filters, startIndex, stopIndex).then((res) => {
      const { status, data: { list } } = res
      if (status === 200) {
        this.setState({
          list: [...this.state.list, ...list]
        })
      }
    })
  }


  // 
  renderList () {
    const { count } = this.state
    return (
      count === 0 ? <NoHouse></NoHouse> : <InfiniteLoader
        isRowLoaded={this.isRowLoaded}
        loadMoreRows={this.loadMoreRows}
        // 
        rowCount={this.state.count}
      >
        {({ onRowsRendered, registerChild }) => (
          <AutoSizer>
            {({ height, width }) => (
              <List
                className={styles.houseList}
                onRowsRendered={onRowsRendered}
                ref={registerChild}
                // 
                rowCount={this.state.count}
                // 
                width={width}
                height={height}
                //  => 
                rowHeight={130}
                // 
                rowRenderer={this.rowRenderer}
              />
            )}
          </AutoSizer>
        )}
      </InfiniteLoader>
    )
  }


  render () {
    return (
      <div className={styles.root}>
        {/* -- */}
        <Filter onFilterSel={this.onFilterSel} />
        {/* = */}
        {this.renderList()}
      </div>
    )
  }
}
