import request from '../request'

/**
 * =
 */
/**
 * 
 * @param {*} id ID
 */
export function getFilters (id) {
  return request.get('/houses/condition', {
    params: { id }
  })
}

/**
 * 
 * @param {*} cityId ID/ID
 * @param {*} filters 
 * @param {*} start 
 * @param {*} end  
 */
export function getListByFilter (cityId, filters, start = 1, end = 20) {
  return request.get('/houses', {
    params: {
      cityId,
      ...filters,
      start,
      end
    }
  })
}

/**
 * 
 * @param {*} id ID
 */
export function getDetail (id) {
  return request.get(`/houses/${id}`)
}

/**
 * 
 * @param {*} formData formData
 */
export function uploadHouseImg (formData) {
  return request.post('/houses/image', formData)
}