import React, { Component } from 'react'
// 
import { Route } from 'react-router-dom'
import { TabBar } from 'antd-mobile'
// 
import Index from '../Index'
import House from '../House'
import Profile from '../Profile'
// 
import './index.css'

// tabBar
import TabBarList from '../../utils/tabBarConfig'
import Hook from '../Hook'

class Home extends Component {
  // 
  state = {
    count: 0,
    // tabBar
    selectedTab: this.props.location.pathname,
  }

  // tabBar
  renderTabBar = () => {
    return (
      <TabBar
        unselectedTintColor="#666"
        tintColor="#33A3F4"
        barTintColor="white">
        {TabBarList.map((item) => (
          <TabBar.Item
            title={item.title}
            key={item.id}
            // 
            icon={<i className={`iconfont ${item.icon}`} />}
            // 
            selectedIcon={<i className={`iconfont ${item.icon}`} />}
            selected={this.state.selectedTab === item.pathname}
            // 
            onPress={() => {
              this.props.history.push(item.pathname)
              // 
              this.setState({
                selectedTab: item.pathname,
              })
            }}
          />
        ))}
      </TabBar>
    )
  }

  render() {
    // console.log(this.props)
    return (
      <div className="home">
        {/*  */}
        <Route exact path="/home" component={Index} />
        <Route path="/home/house" component={House} />
        <Route path="/home/profile" component={Profile} />
        {/* react-hook */}
        <Route path="/home/hook" component={Hook} />

        {/* tabBar */}
        <div className="tabBarBox">{this.renderTabBar()}</div>
      </div>
    )
  }
}

export default Home
