import React, { Component } from 'react'

import { Link } from 'react-router-dom'
import { Grid, Button, Modal, Toast } from 'antd-mobile'

import { BASE_URL } from '../../utils/request'

import styles from './index.module.css'
import { isAuth, delToken } from '../../utils'
import { getUserData, logout } from '../../utils/api/user'

// 
const menus = [
  { id: 1, name: '', iconfont: 'icon-coll', to: '/favorate' },
  { id: 2, name: '', iconfont: 'icon-ind', to: '/rent' },
  { id: 3, name: '', iconfont: 'icon-record' },
  {
    id: 4,
    name: '',
    iconfont: 'icon-identity',
  },
  { id: 5, name: '', iconfont: 'icon-myinfo' },
  { id: 6, name: '', iconfont: 'icon-cust' },
]

// 
const DEFAULT_AVATAR = BASE_URL + '/img/profile/avatar.png'

export default class Profile extends Component {
  state = {
    // 
    isLogin: isAuth(),
    // 
    userInfo: null,
  }

  componentDidMount() {
    this.getUserInfo()
  }

  // ==
  async getUserInfo() {
    const { isLogin } = this.state
    if (isLogin) {
      // 
      const { status, data, description } = await getUserData()
      // console.log(status, data)
      if (status === 200) {
        // 
        // console.log(typeof data.avatar)
        data.avatar = !!data.avatar ? BASE_URL + data.avatar : null
        this.setState({
          userInfo: data,
        })
      } else {
        // token
        if (status === 400) {
          Toast.fail(description, 2, () => {
            this.props.history.replace('/login')
          })
        }
      }
    }
  }

  // 
  /**
   * =
   */
  logout = () => {
    Modal.alert('', '???', [
      {
        text: '',
      },
      {
        text: '',
        onPress: async () => {
          console.log('')
          // 
          /**
           * 
           * 1. 
           * 2. token()
           */
          const { status, description } = await logout()
          if (status === 200) {
            Toast.success(description, 2)
            // token
            delToken()
            // =
            this.setState({
              isLogin: false,
              userInfo: null,
            })
          } else {
            Toast.fail(description, 2)
          }
        },
      },
    ])
  }

  render() {
    const { history } = this.props
    const { userInfo, isLogin } = this.state

    return (
      <div className={styles.root}>
        {/*  */}
        <div className={styles.title}>
          <img
            className={styles.bg}
            src={BASE_URL + '/img/profile/bg.png'}
            alt=""
          />
          {/*  */}
          <div className={styles.info}>
            <div className={styles.myIcon}>
              {/*  */}
              <img
                className={styles.avatar}
                src={userInfo?.avatar ? userInfo?.avatar : DEFAULT_AVATAR}
                alt="icon"
              />
            </div>
            <div className={styles.user}>
              <div className={styles.name}>{userInfo?.nickname || ''}</div>
              {/*  */}
              {isLogin ? (
                <>
                  <div onClick={this.logout} className={styles.auth}>
                    <span></span>
                  </div>
                  <div
                    className={styles.edit}
                    onClick={() => history.push('/rent/add')}>
                    
                    <span className={styles.arrow}>
                      <i className="iconfont icon-arrow" />
                    </span>
                  </div>
                </>
              ) : (
                <div className={styles.edit}>
                  <Button
                    type="primary"
                    size="small"
                    inline
                    onClick={() => history.push('/login')}>
                    
                  </Button>
                </div>
              )}

              {/*  */}
            </div>
          </div>
        </div>

        {/*  */}
        <Grid
          data={menus}
          columnNum={3}
          hasLine={false}
          renderItem={(item) =>
            item.to ? (
              <Link to={item.to}>
                <div className={styles.menuItem}>
                  <i className={`iconfont ${item.iconfont}`} />
                  <span>{item.name}</span>
                </div>
              </Link>
            ) : (
              <div className={styles.menuItem}>
                <i className={`iconfont ${item.iconfont}`} />
                <span>{item.name}</span>
              </div>
            )
          }
        />

        {/*  */}
        <div className={styles.ad}>
          <img src={BASE_URL + '/img/profile/join.png'} alt="" />
        </div>
      </div>
    )
  }
}
