/**
 * axios
 */
import axios from 'axios'
// 
import { Toast } from 'antd-mobile'
import { getToken } from '.'

// axios
// 
// const BASE_URL = 'http://api-haoke-dev.itheima.net'
const BASE_URL = 'https://api-haoke-web.itheima.net'
const MyAxios = axios.create({
  baseURL: BASE_URL
})

// 
// Add a request interceptor
// 
MyAxios.interceptors.request.use(function (config) {
  // Do something before request is sent
  // console.log('')
  Toast.loading('...', 0)
  // url=/user=token
  let rurl = config.url, whiteName = ['/user/registered', '/user/login']
  if (rurl.startsWith('/user') && !whiteName.includes(rurl)) {
    config.headers.authorization = getToken()
  }
  // console.log(config)

  return config
}, function (error) {
  // Do something with request error
  return Promise.reject(error)
})

// Add a response interceptor
// 
MyAxios.interceptors.response.use(function (response) {
  // Any status code that lie within the range of 2xx cause this function to trigger
  // Do something with response data
  // console.log('', response)
  // loading
  Toast.hide()
  // 
  const { data: { status, description, body } } = response
  return {
    status,
    description,
    data: body
  }
}, function (error) {
  // Any status codes that falls outside the range of 2xx cause this function to trigger
  // Do something with response error
  return Promise.reject(error)
})

export { BASE_URL }

export default MyAxios