import React, { Component } from 'react'
// 
import { Carousel, Flex, Grid, WingBlank, SearchBar } from 'antd-mobile'

// 
import Navs from '../../utils/navs'

// axios
import { getSwiper, getGrid, getNews } from '../../utils/api/home'
// 
import './index.scss'
import { BASE_URL } from '../../utils/request'
import { getCurrCity } from '../../utils'

/**
 * 
 */
export default class Index extends Component {
  state = {
    // 
    swiper: [
      // { id: 0, imgSrc: '12.png' },
      // { id: 1, imgSrc: '12.png' },
    ],
    // 
    groups: [],
    // 
    news: [],
    // 
    keyword: '',
    // 
    imgHeight: 212,
    // 
    isPlay: false,
    // 
    cityInfo: { label: '--', value: '' },
  }

  componentDidMount() {
    this.getAllDatas()
    this.getCity()
  }

  // 
  async getCity() {
    const res = await getCurrCity()

    console.log('', res)
    this.setState({
      cityInfo: res,
    })
  }

  // =
  getAllDatas() {
    // Promise.all(array)=>promise
    const promises = [getSwiper(), getGrid(), getNews()]
    Promise.all(promises).then((res) => {
      console.log('Promise', res)
      const [swiper, groups, news] = res
      // 
      this.setState(
        {
          swiper: swiper.data,
          groups: groups.data,
          news: news.data,
        },
        () => {
          this.setState({ isPlay: true })
        }
      )
    })
  }

  // 
  renderSwiper = () => {
    return (
      <Carousel autoplay={this.state.isPlay} infinite>
        {this.state.swiper.map((item) => (
          <a
            key={item.id}
            href="http://www.itheima.com"
            style={{
              display: 'inline-block',
              width: '100%',
              height: this.state.imgHeight,
              background: '#eee',
            }}>
            <img
              src={`http://api-haoke-dev.itheima.net${item.imgSrc}`}
              alt=""
              style={{ width: '100%', verticalAlign: 'top' }}
              onLoad={() => {
                // =
                // fire window resize event to change height
                window.dispatchEvent(new Event('resize'))
                this.setState({ imgHeight: 'auto' })
              }}
            />
          </a>
        ))}
      </Carousel>
    )
  }

  // 
  renderNavs = () => {
    return (
      <Flex className="nav">
        {Navs.map((item) => (
          <Flex.Item
            key={item.id}
            onClick={() => {
              // 
              this.props.history.push(item.url)
            }}>
            <img alt="" src={item.icon} />
            <p>{item.title}</p>
          </Flex.Item>
        ))}
      </Flex>
    )
  }

  // 
  renderGrid = () => {
    return (
      <Grid
        data={this.state.groups}
        columnNum={2}
        hasLine={false}
        square={false}
        renderItem={(item) => (
          // item
          <Flex className="grid-item" justify="between">
            <div className="desc">
              <h3>{item.title}</h3>
              <p>{item.desc}</p>
            </div>
            <img src={`${BASE_URL}${item.imgSrc}`} alt="" />
          </Flex>
        )}
      />
    )
  }

  // 
  renderNews() {
    return this.state.news.map((item) => (
      <div className="news-item" key={item.id}>
        <div className="imgwrap">
          <img className="img" src={`${BASE_URL}${item.imgSrc}`} alt="" />
        </div>
        <Flex className="content" direction="column" justify="between">
          <h3 className="title">{item.title}</h3>
          <Flex className="info" justify="between">
            <span>{item.from}</span>
            <span>{item.date}</span>
          </Flex>
        </Flex>
      </div>
    ))
  }

  // 
  renderTopNav = () => {
    return (
      <Flex justify="around" className="topNav">
        <div className="searchBox">
          {/*  */}
          <div
            className="city"
            onClick={() => {
              this.props.history.push('/cityList')
            }}>
            {this.state.cityInfo.label}
            <i className="iconfont icon-arrow" />
          </div>
          {/* input */}
          <SearchBar
            value={this.state.keyword}
            onChange={(v) => this.setState({ keyword: v })}
            placeholder=""
          />
        </div>
        {/*  */}
        <div
          className="map"
          onClick={() => {
            this.props.history.push('/map')
          }}>
          <i key="0" className="iconfont icon-map" />
        </div>
      </Flex>
    )
  }

  // 
  render() {
    return (
      <div className="Index">
        {/*  */}
        {this.renderTopNav()}
        {/*  */}
        {this.renderSwiper()}
        {/*  */}
        {this.renderNavs()}
        {/*  */}
        <div className="group">
          {/* title */}
          <Flex className="group-title" justify="between">
            <h3></h3>
            <span></span>
          </Flex>
          {/* content */}
          {this.renderGrid()}
        </div>
        {/*  */}
        <div className="news">
          <h3 className="group-title"></h3>
          <WingBlank size="md">{this.renderNews()}</WingBlank>
        </div>
      </div>
    )
  }
}
